import { useEffect, useState } from "react";
import { Button, Col, Form, Image, ListGroup, Row } from "react-bootstrap";
import { AiFillDelete } from "react-icons/ai";
import { useSelector, useDispatch } from "react-redux";
import { removeFromCart, chnageQuantity } from "../features/cart/cartSlice";

const Cart = () => {
  const cart = useSelector((state) => state.cart.cart);
  const dispatch = useDispatch();

  const [total, setTotal] = useState();

  useEffect(() => {
    if (cart.length > 0) {
      setTotal(
        cart.reduce((acc, curr) => acc + Number(curr.cart.price) * curr.qty, 0)
      );
    } else {
      setTotal(0);
    }
  }, [cart]);

  return (
    <div className="home">
      <div className="productContainer">
        <ListGroup>
          {cart.map((product) => (
            <ListGroup.Item key={product.cart.id}>
              <Row>
                <Col xs={2} sm={2}>
                  <Image
                    src={product.cart.image}
                    alt={product.cart.name}
                    fluid
                    rounded
                  />
                </Col>
                <Col xs={3}>
                  <span>{product.cart.name}</span>
                </Col>
                <Col xs={2}>₹ {Math.trunc(product.cart.price)}</Col>
                <Col xs={3} sm={3}>
                  <Form.Control
                    as="select"
                    value={product.qty}
                    onChange={(e) =>
                      dispatch(
                        chnageQuantity({
                          id: product.cart.id,
                          qty: e.target.value,
                        })
                      )
                    }
                  >
                    {[...Array(product.cart.inStock).keys()].map((x) => (
                      <option key={x + 1}>{x + 1}</option>
                    ))}
                  </Form.Control>
                </Col>
                <Col xs={2} sm={2}>
                  <Button
                    type="button"
                    variant="light"
                    onClick={() => dispatch(removeFromCart(product.cart))}
                  >
                    <AiFillDelete fontSize="20px" />
                  </Button>
                </Col>
              </Row>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </div>
      <div className="filters bg-primary d-md-flex flex-column text-center text-light pt-0 pb-0 pt-md-5 px-1 px-md-4">
        <span className="title">Subtotal ({cart.length}) items</span>
        <span style={{ fontWeight: 700, fontSize: 20 }}>Total: ₹ {total}</span>
        <Button type="button" disabled={cart.length === 0}>
          Proceed to Checkout
        </Button>
      </div>
    </div>
  );
};

export default Cart;
